import ko from 'knockout'

class FilmsViewModel {
    
    constructor() {
        this.films = ko.observableArray([])
        this.currentState = ko.observable('')
        this.film = ko.observable({
            title:undefined,
            description:undefined,
            releaseYear:undefined
        })

        this.viewFilms()
    }

    viewFilms = () => {
        this.currentState('LIST')

        let filter = {
            order: 'filmId desc'
        }

        $.ajax(`http://adamantio.com.br:4040/api/Films?filter=${JSON.stringify(filter)}`)
        .done((films) => {
            this.films(films)
        })

    }

    createNewFilm = () => {
       this.currentState('CREATE')
       this.film({
        title: undefined,
        description: undefined,
        releaseYear: undefined
       })

    }

    save = () => {
        let filmToSave = ko.toJS(this.film)

        $.ajax({
            method: 'POST',
            url: 'http://adamantio.com.br:4040/api/Films',
            data: filmToSave
        }).done(() => {
            this.viewFilms() 
        }).fail(error => {
            console.error(error)
            alert('Failed to save')
        })
    }

    cancel = () => {
        this.viewFilms()
    }

}

$(document).ready(() => {
    ko.applyBindings(new FilmsViewModel())
})